<template>
  <div class='dexview'>
    <Pokemon :pokemon='getSelectedPokemon'></Pokemon>

    <section class='dexview__list'>
      <!--NAVIGATION BOX-->
      <NavBox :multiplier='multiplier' @update-multiplier='updateMultiplier' />

      <!--SEARCH-->
      <section class='dexview__entry__search'>
        <input type='text' @keyup='search_for' v-model='searchText' />
      </section>

      <!--POKEMON LIST-->
      <PkmnList
        :list='(searched || battledex)'
        :start='start'
        :end='end'
        :multiplier='multiplier'
        @reset-search='resetSearch'
      />
    </section>
  </div>
</template>

<script>
import NavBox from "./_navBox.module";
import PkmnList from "./_pkmnlist.module";
import Pokemon from "@/components/Pokemon";

export default {
  components: {
    Pokemon,
    NavBox,
    PkmnList
  },
  data() {
    return {
      start: 108,
      end: 108,
      multiplier: 1,
      searched: false,
      searchText: "",
      tier: "",
      tiers: [
        { tier: "Over Used", alias: "OU", path: "ou" },
        { tier: "Under Used", alias: "UU", path: "uu" },
        { tier: "Battle Stadium Singles", alias: "BSS", path: "bss" },
        { tier: "Ubers", alias: "UBERS", path: "ubers" },
        { tier: "Video Game Championship", alias: "VGC", path: "vgc" },
        { tier: "Rarely Used", alias: "RU", path: "ru" },
        { tier: "Monotype", alias: "Monotype", path: "monotype" },
        { tier: "Little Cup", alias: "LC", path: "lc" },
        { tier: "Not Fully Evolved", alias: "NFE", path: "nfe" }
      ]
    };
  },
  methods: {
    updateMultiplier(val) {
      this.multiplier = val;
    },
    search_for(ev) {
      const word = ev.target.value.toLowerCase();
      let s = [];
      for (let pkmn in this.battledex) {
        if (word.length > this.battledex[pkmn].slug.length) continue;
        else if (this.battledex[pkmn].slug.startsWith(word)) {
          s.push(this.battledex[pkmn]);
        }
      }
      this.searched = s;
    },
    resetSearch() {
      this.searched = false;
      this.searchText = "";
    }
  },
  created() {
    this.tier = this.$route.params.tier;
  }
};
</script>
