<template>
  <!--NAVIGATION BOX-->
  <div class='navbox'>
    <div class='navbox-back' @click='go(-1)'></div>
    <div class='navbox-box'>
      box {{multiplier}} of
      <select @change='shiftTiers' v-model='tier'>
        <option v-for='(tier, key) in tiers' :key='key' :value='tier.path'>{{tier.path}}</option>
      </select>
    </div>
    <div class='navbox-go' @click='go(1)'></div>
  </div>
</template>


<script>
export default {
  props: ["multiplier"],
  data() {
    return {
      tier: "",
      tiers: [
        { tier: "Ubers", alias: "UBERS", path: "ubers" },
        { tier: "Over Used", alias: "OU", path: "ou" },
        { tier: "Under Used", alias: "UU", path: "uu" },
        { tier: "Rarely Used", alias: "RU", path: "ru" },
        { tier: "Never Used", alias: "NU", path: "nu" },
        { tier: "PU", alias: "PU", path: "pu" },
        { tier: "Monotype", alias: "Monotype", path: "monotype" },
        { tier: "Little Cup", alias: "LC", path: "lc" },
        { tier: "Not Fully Evolved", alias: "NFE", path: "nfe" },
        { tier: "Video Game Championship", alias: "VGC", path: "vgc" },
        { tier: "Battle Stadium Singles", alias: "BSS", path: "bss" },        
      ]
    };
  },
  methods: {
    go(value) {
      value = this.multiplier + value;
      if (value <= 0) {
        return;
      }
      if (value > Math.ceil(this.battledex_size / 108)) {
        return;
      }
      this.$emit("update-multiplier", value);
    },
    shiftTiers(e) {
      const tier = e.target.value;
      this.$router.push(`${tier}`);
    }
  },
  created(){
      this.tier = this.$route.params.tier
  }
};
</script>

