<template>
  <div class='pkmnlist'>
    <div
      v-for='(val, k) in list'
      :key='k'
      v-show='k >= (start*multiplier)-108 && k < (end*multiplier)'
      :class='"pokemon-"+ val.types[0] +" pokemon-"+ val.types[0] +"-border"'
      class='pkmnlist-item'
      @click='choosePokemon(val.slug)'
    >
      <div>
        <img v-show='val.url' :src='"/2d/"+val.slug+".png"' @load='val.url=true' />
      </div>
    </div>
  </div>
</template>

<script>

import axios from "axios";
export default {
  props: ["list", "start", "end", "multiplier"],
  methods: {
    choosePokemon(pokemon) {
      this.$emit("reset-search");
      axios.get(`/pokemon/${this.$route.params.tier}/${pokemon}`).then(res => {
        this.$store.commit("dex/SET_SELECTED_POKEMON", res.data.pokemon);
      });
    }
  },
  created(){
    this.$store.commit("dex/SET_LOADING_STATE", false);
    this.choosePokemon(this.battledex[0].slug);
  }
};
</script>

