<template>
  <TierView v-if='isDexLoaded'></TierView>
  <section v-else class='explore__items'>
    <div class='lds-ellipsis'>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  </section>
</template>
<script>
import TierView from "@/components/TierView";
import axios from "axios";

export default {
  title: "Battle Dex",
  components: {
    TierView
  },
  methods: {
    collectData() {
      if (!this.isDexLoaded) {
        const tier = this.tier;
        axios.get(`/pokemon/${tier}`).then(res => {
          this.$store.commit("dex/SET_SELECTED_POKEMON", res.data.pokemon[0]);
          this.$store.commit("dex/SET_BATTLEDEX", res.data.pokemon);
        });
      }
    }
  },
  computed: {
    tier() {
      return this.$route.params.tier;
    },
    isLoading() {
      return this.$store.getters["dex/isLoading"];
    }
  },
  created() {
    this.collectData();
  },
  watch: {
    tier() {
      this.$store.commit("dex/SET_BATTLEDEX", false);
      this.collectData();
    }
  }
};
</script>